.porfolios-img {
  position: relative;
}

.porfolios-img::before {
  content: "";
  position: absolute;
  left: 2%;
  top: 3%;
  height: 0;
  width: 0;
  transition: all .4s ease-in-out;
}

.porfolios-img ul {
  position: absolute;
  left: 50%;
  top: 40%;
  /* transform: translate(-50%, -50%); */
  opacity: 0;
  transition: all .4s ease-in-out;
}

.porfolios-img ul li {
  background-color: #ffbb32;
  padding: .4rem;
  border-radius: 50%;
  margin: 0 .5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .4s ease-in-out;
}

.porfolios-img ul li a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000;
  font-size: 24px;
  transition: all .2s ease-in-out;
}

.porfolios-img ul li:hover a {
  color: #fff;
}

.porfolios-img:hover ul {
  display: block;
  transform: translateY(0);
  transform: translate(-50%, 40%);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .4s ease-in-out;
  opacity: 1;
}

.porfolios-img:hover::before {
  height: calc(100% - 7%);
  width: calc(100% - 4%);
  background-color: #b5b5b5;
  opacity: 0.7;
  transform-origin: left;
  transition: all .4s ease-in-out;
}

